<template lang="pug">
div
  h1(v-if="user && role") 你好！{{name}}
  h1(v-else) 九型人格测试
  ul
    li 1.这是目前最权威的测试系统，共108个题目
    li 2.请在符合你的题目后面点“是”
    li 3.最后结果中占比最高的就是你的人格类型
    li 4.这只是测试你的人格类型，并无好坏之分
  .bar.check(v-if="user && role")
    template(v-if="!user.get('subject')")
      span.links(v-if="role == 'self'")
        router-link(to="/dad/result") 查看爸爸
        router-link(to="/mom/result") 查看妈妈
      router-link(to="/self/result" v-else) 查看孩子
    router-link(:to="`/${role}/result`" v-if="finished") 查看结果
    router-link(:to="goOn" v-else) {{user.get(role).length > 0 ? '继续' : '开始'}}测试
  .bar.login(v-else)
    router-link(to="/register/parent") 家长
    router-link(to="/register/student") 学生
    router-link(to="/register/teacher") 老师
</template>

<script>
import AV from '@/api/AV'
import {trans_name} from '@/assets/utils'

export default {
  data() {
    return {
      user: AV.User.current(),
      role: localStorage.role
    }
  },
  computed: {
    name() {
      return trans_name(this.user, this.role)
    },
    finished() {
      return this.user.get(this.role).length >= 108
    },
    goOn() {
      const n = this.user.get(this.role).length + 1
      return `/${this.role}/topics/${n}`
    }
  }
}
</script>

<style lang="css" scoped>
h1 {
  text-align: center;
  font-size: 24px;
  letter-spacing: 2px;
}
ul {
  margin-left: 8px;
  margin-right: 8px;
  border-top: solid 1px #bae7ff;
  border-bottom: solid 1px #bae7ff;
}
li {
  margin-top: 8px;
  margin-bottom: 8px;
}
.bar {
  margin-top: 16px;
}
.check a:last-child {
  flex-grow: 1;
  text-align: right;
}
.links a {
  margin-right: 16px;
}
.login {
  font-size: 20px;
  letter-spacing: 2px;
}
</style>



