import AV from './AV'

export async function login(username, role) {
  const st = await find(username)
  if (st) {
    const user = await AV.User.logIn(username, 'xfkj')
    localStorage.role = role
    return user
  } else {
    const s = new AV.User()
    s.setUsername(username)
    s.setPassword('xfkj')
    const user = await s.signUp()
    localStorage.role = role
    return user
  }
}

export async function login_teacher({username, subject, role}) {
  const existed_user = await find(username)
  if (existed_user) {
    const user = await AV.User.logIn(username, 'xfkj')
    const login_user = user.save({subject})
    localStorage.role = role
    return login_user
  } else {
    const user = new AV.User()
    user.setUsername(username)
    user.setPassword('xfkj')
    user.set('subject', subject)
    const login_user = await user.signUp()
    localStorage.role = role
    return login_user
  }
}

function find(username) {
  const users = new AV.Query('_User')
  return users.equalTo('username', username).first()
}



