<template lang="pug">
div
  .item
    label(for="username") 姓名：
    input(id="username" v-model.trim="username" type="text" spellcheck="false" placeholder="你的名字")
  .item
    label(for="subject") 科目：
    select(v-model="subject" id="subject")
      option(disabled value="") 请选择
      option 语文
      option 数学
      option 英语
      option 政治
      option 历史
      option 地理
      option 物理
      option 化学
      option 生物
      option 技术
      option 其他
  button(@click="start" :disabled="loading") {{loading ? '进入中...' : '开 始'}}
</template>

<script>
import {login_teacher} from '@/api/users'

export default {
  data() {
    return {
      username: '',
      subject: '',
      loading: false,
    }
  },
  methods: {
    start() {
      const {username, subject} = this

      if(!username || !subject) return alert('所有字段必填')
      if(/\w/.test(username)) return alert('名字必须为中文')
      console.log(username, subject);

      this.loading = true
      login_teacher({username, subject, role: 'self'})
      .then(user => {
        const n = user.get('self').length + 1
        this.loading = false
        this.$router.push('/self/topics/' + n)
      })
      .catch(err => {
        this.loading = false
        alert(err)
      })
    }
  }
}
</script>

<style lang="css" scoped>
.item {
  width: 80%;
  margin: auto;
  margin-top: 16px;
  display: flex;
}
.item input {
  flex-grow: 1;
}
button {
  width: 100%;
  margin-top: 240px;
  text-align: center;
  font-size: 24px;
}
select {
  flex-grow: 1;
  font-size: 16px;
  color: inherit;
  border: solid 1px #bae7ff;
  border-radius: 4px;
  background-color: transparent;
}
</style>



